/*******************************************
	BASE ELEMENT STYLES
*******************************************/
* {
  box-sizing: border-box;
}

body {
  background: #78919c;
  color: #fff;
  font-family: Georgia, serif;
}

h1 {
  font-family: "Black Ops One", serif;
  font-size: 3em;
  font-weight: 900;
  margin-bottom: 0;
}

h2 {
  margin-top: 0;
  color: #e5e4c0;
  text-shadow: 0 0 5px #555;
  letter-spacing: 2px;
  font-size: 2em;
}

div,
button {
  margin: 0 auto;
}

button {
  display: inline-block;
  font-weight: 700;
  color: #7a8b95;
  padding: 16px;
  margin-bottom: 8px;
  background: #b8e5f5;
  background: -webkit-linear-gradient(#b8e5f5, #aad3e1);
  background: linear-gradient(#b8e5f5, #aad3e1);
  cursor: pointer;
  border: none;
  border-radius: 8px;
  box-shadow: 0 5px 0 #8eb4c1;
  outline: none !important;
}

button:hover {
  background: #bdecfc;
  background: -webkit-linear-gradient(#bdecfc, #b4dfee);
  background: linear-gradient(#bdecfc, #b4dfee);
  box-shadow: 0 4px 0 #9ec7d5;
}

button:active {
  position: relative;
  top: 4px;
  box-shadow: none;
}

form {
  text-align: center;
}

/*******************************************
	CONTAINER STYLES
*******************************************/

#wrapper {
  background: rgba(255, 255, 255, 0.1);
  margin: 0 auto;
  padding-right: 12px;
  padding-left: 12px;
  box-shadow: inset 0 0 5px #eee;
  overflow: hidden;
}

.container {
  position: relative;
}

#title-container {
  padding-top: 40px;
  margin-top: 40px;
  text-align: center;
}

/*******************************************
	USER INTERFACE STYLES
*******************************************/

.letterpress {
  color: #496471;
  text-shadow: 1px 1px rgba(255, 255, 255, 0.2), -1px -1px rgba(0, 0, 0, 0.5);
}

#output {
  position: relative;
  font-size: 1.25em;
  margin-top: 10%;
  width: 70%;
  text-align: center;
  min-height: 240px;
  right: 150%;
}

/*******************************************
		Button Styles
	*******************************************/

.btn-group {
  text-align: center;
  padding: 8px 0;
  padding-top: 16px;
}

.btn-group button {
  margin-right: 8px;
  margin-left: 8px;
}

/*******************************************
	ANIMATION STYLES
*******************************************/

.animate {
  -webkit-transition: all 1s;
  transition: all 1s;
}

.slide-off-left {
  left: -150%;
}

.slide-in-right {
  left: 0;
}

p {
  text-align: center;
  font-size: 20px;
}

/*******************************************
	RESPONSIVE STYLES
*******************************************/

@media (min-width: 765px) {
  #wrapper {
    width: 70%;
    max-width: 1280px;
  }
}
